:host {
  @apply block h-full;

  ::ng-deep {
    // Ensure smooth transitions
    .p-splitter-panel {
      @apply transition-all duration-300 ease-in-out;
    }

    // Make sure nested splitters don't have double borders
    app-adaptive-layout app-adaptive-layout .p-splitter {
      @apply border-0;
    }

    // Panel transition utility class - defined directly instead of using @layer
    .panel-transition {
      @apply transition-all duration-300 ease-in-out;
    }

    p-splitter {
      @apply h-full;
    }

    .details-hidden-state {
      .p-splitterpanel:nth-child(3),
      .p-splitter-panel:nth-child(3) {
        @apply hidden;
      }

      .p-splitter-gutter {
        @apply hidden;
      }

      .p-splitter-panel:first-of-type {
        @apply w-full max-w-full;
        @apply flex-1;
      }
    }

    .search-highlighted {
      background-color: rgba(255, 235, 59, 0.3) !important;
      border-left: 3px solid var(--primary-color, #3b82f6) !important;

      .p-treenode-content {
        background-color: rgba(255, 235, 59, 0.2) !important;
        font-weight: 600 !important;
      }
    }

    .search-highlighted:hover {
      background-color: rgba(255, 235, 59, 0.4) !important;
    }
  }

  ::ng-deep app-adaptive-layout {
    height: 100%;
    overflow: hidden;
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;

    .p-splitter {
      height: 100%;
      overflow: hidden;
      width: 100% !important;
      max-width: 100% !important;
    }

    .p-splitterpanel {
      overflow: hidden;
      min-width: 0;
      max-width: 100%;
      box-sizing: border-box;

      // Target both scrollable and non-scrollable content
      > div {
        height: 100%;
        min-width: 0;
        width: 100% !important;
        max-width: 100% !important;
        box-sizing: border-box;

        // Force overflow-auto to respect width
        &.overflow-auto {
          overflow-y: auto;
          overflow-x: hidden !important;

          // Ensure children also respect width
          > * {
            max-width: 100%;
            box-sizing: border-box;
          }
        }
      }

      &:last-child {
        min-width: 0;

        > div {
          overflow: hidden;
        }
      }
    }

    // Constrain accordion and its children
    p-accordion,
    .p-accordion {
      width: 100% !important;
      max-width: 100% !important;
      min-width: 0 !important;
      overflow: hidden !important;
    }

    .p-accordion-header,
    .p-accordion-content,
    .p-accordion-panel {
      width: 100% !important;
      max-width: 100% !important;
      min-width: 0 !important;
      box-sizing: border-box !important;
      overflow: hidden !important;
    }
  }

  app-adaptive-layout app-adaptive-layout {
    height: 100%;
    overflow: hidden;

    .p-splitter {
      height: 100%;
      overflow: hidden;
    }

    .p-splitter-panel {
      overflow: hidden;
      min-width: 0; // Critical: Allow panels to shrink below content width

      // Force scrolling behavior
      > div {
        overflow: auto;
        height: 100%;
        min-width: 0; // Critical: Allow content to shrink
      }

      // Specific rule for details panel
      &:last-child {
        min-width: 0;

        > div {
          overflow: hidden; // Details panel manages its own overflow
        }
      }
    }
  }

  // Ensure the parent is relative and has overflow visible
  .h-full.flex.relative {
    position: relative;
    overflow: visible;
  }

  // Responsive styles
  @media (max-width: 768px) {
    // On mobile, ensure no horizontal scroll
    app-adaptive-layout {
      overflow-x: hidden;
    }

    .p-splitter-panel {
      min-width: 0 !important;
    }
  }
}

.has-details {
  ::ng-deep {
    .main-layout {
      .p-splitter-gutter {
        @apply cursor-col-resize z-10;
      }
    }
  }
}

::ng-deep .load-more-node {
  .p-treenode-content {
    font-style: italic;
    color: var(--primary-color); // Or another distinct color
    cursor: pointer;

    &:hover {
      background-color: var(--surface-hover);
    }
  }
  // Ensure spinner aligns well
  .p-treenode-icon {
    margin-right: 0.5rem;
  }
}
